<template>
    <div>
        <v-container>
            <v-container>
                <label class="display-1">
                    Registrovanje lekova
                </label>
            </v-container>
            <v-row
                align="center"
                justify-content="center"
            >
                <v-col> 
                    <v-form ref="forma">
                        <v-card>
                            <v-container>
                                <v-text-field
                                    label="ifra leka"
                                    :rules="rules"
                                    type="number"
                                    required
                                    v-model="sifraLeka"
                                >
                                </v-text-field>
                                <v-text-field
                                    label="Naziv leka"
                                    :rules="rules"
                                    required
                                    v-model="nazivLeka"
                                >
                                </v-text-field>
                                <v-textarea
                                    label="Opis leka"
                                    :rules="rules"
                                    required
                                    v-model="opisLeka"
                                >
                                </v-textarea>
                                <v-row>
                                    <v-col
                                        cols="3"
                                        class="ml-auto"
                                    >
                                        <v-btn @click="registruj" color="success">
                                            Registruj lek
                                        </v-btn>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card>
                    </v-form>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
    /*
     * Funkcija koja proverava da li se string sastoji samo od brojeva i slova
     */
    function isAlphaNumeric(str) {
        var code, i, len;

        for (i = 0, len = str.length; i < len; i++) {
            code = str.charCodeAt(i);
            if (!(code > 47 && code < 58) && // numeric (0-9)
                !(code > 64 && code < 91) && // upper alpha (A-Z)
                !(code > 96 && code < 123)) { // lower alpha (a-z)
            return false;
            }
        }
        return true;
    }

    const axios = require('axios');
    export default {
        name:"RegLekova",
        data:function () {
            return {
                rules:[v=>!!v||"Ovo polje je obavezno!"],
                sifraRules:[
                    v=>!!v||"Ovo polje je obavezno!"
                    , v=>v.length!=12||"ifra mora biti duine 12 karaktera."
                    , v=>v.toUpperCase()!=v||"ifra mora sadrati samo velika slova."
                    , v=>!isAlphaNumeric(v)||"ifra mora sadrati samo brojeve i slova."
                ],
                sifraLeka:"",
                nazivLeka:"",
                opisLeka:""
            }
        },
        methods:{
            registruj:function () {
                if(!this.$refs.forma.validate()){
                    return;
                }
                axios.post("/lek", {nazivLeka:this.nazivLeka, opisLeka:this.opisLeka, sifraLeka:this.sifraLeka})
                    .then(()=>{
                        this.$store.commit("setSnackbar", {text:"Uspeno registrovan lek.", color: "success"});

                        this.$router.push("/pregledLekova");
                    })
                    .catch(error=>{
                        this.$store.commit("setSnackbar", {text:"Izvinjavamo se dolo je do greke.", color: "error"});
                        console.log(error);   
                        this.$router.push("/pregledLekova");
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                 
                    })
            },
        }
    }
</script>